import logo from "./logo.svg";
import "./App.css";
import { useState } from "react";
import ListItems from "./ListItems";

function App() {
  const [item, setItem] = useState([]);
  const [currentItem, setCurrentItem] = useState({
    text: "",
    key: "",
  });
  const handleInput = (e) => {
    setCurrentItem({
      text: e.target.value,
      key: Date.now(),
    });
  };

  const addItem = (e) => {
    e.preventDefault();
    const newItem = currentItem;
    if (newItem !== "") {
      const newItems = [...item, newItem];
      setItem(newItems);
      setCurrentItem({
        text: "",
        key: "",
      });
    }
  };

  const deleteItem = (key) => {
    const filterItems = item.filter((item) => item.key !== key);
    setItem(filterItems);
  };

  const setUpdate = (text, key) => {
    const items = item;
    items.map((item) => {
      if (item.key === key) {
        item.text = text;
        console.log(text);
      }
    });
    setItem([...items]);
  };

  return (
    <>
      <div className="app">
        <header>
          <form id="to-do-form" action="#" onSubmit={addItem}>
            <input
              type="text"
              placeholder="Enter Text"
              value={currentItem.text}
              onChange={handleInput}
            />

            <button type="submit">Add</button>
          </form>
        </header>
        <ListItems item={item} deleteItem={deleteItem} setUpdate={setUpdate} />
      </div>
    </>
  );
}

export default App;
